import React, { useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useParams } from 'react-router-dom';
import Icon from '../../../components/Icon';
import style from './ContactDetails.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { deleteContact as deleteContactApi, getContactById } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import MiniBox from '../../../layout/MiniBox';
import { useDrawer } from '../../../hooks/drawer';
import { eventEmitter, Events } from '../../../utils/events';

const AddContact = React.lazy(() => import('../AddContact/AddContact'));

const ContactDetails = () => {
  const { t } = useTranslation();
  const { close, open } = useDrawer();
  const dispatch = useAppAsyncDispatch();
  const { id } = useParams<{
    id: string
  }>();

  const contact = useAppSelector((store) => store.contact.selectedContact, shallowEqual);

  const address = useMemo(() => {
    const a = contact?.check?.address || contact?.card?.address;
    return a ? (
      `${a.line1
      },${
        a.city
      },${
        a.state},${a.postalCode}`
    ) : 'NA';
  }, [contact]);

  const deleteContact = useCallback(() => {
    if (contact?.id) {
      const contactId = contact?.id;
      dispatch(deleteContactApi(contactId))
        .then(() => {
          eventEmitter.dispatch(Events.CONTACT_REFRESH, {});
          close();
        }).catch(apiErrorHandler);
    }
  }, [contact]);

  useEffect(() => {
    if (id) {
      dispatch(getContactById(id))
        .catch(apiErrorHandler);
    }
  }, [id]);

  if (!contact) {
    return null;
  }
  return (
    <Box className="page">
      <MiniBox
        heading={t('contact_details.title')}
        showBack
        onBack={() => {
          close();
        }}
      >
        <Box width="100%" paddingTop="15px">
          <Box border="1px solid var(--border-color-1)" className={style.detailsInfoBox}>
            <Box className={style.accountValueInfo}>
              <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('contact_details.phone')}</Text>
              <Text variant="regular" fontSize="sm" w="60%" textAlign="left">{contact.phone || 'NA'}</Text>
            </Box>
            <Box className={style.accountValueInfo}>
              <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('contact_details.emailId')}</Text>
              <Text variant="regular" fontSize="sm" w="60%" textAlign="left">{contact.email || 'NA'}</Text>
            </Box>
            <Box className={style.accountValueInfo}>
              <Text variant="regular" w="40%" className="text-light" textAlign="left">{t('contact_details.address')}</Text>
              <Text variant="regular" fontSize="sm" w="60%" textAlign="left">{address}</Text>
            </Box>
          </Box>

          <Text variant="large" mb="16px">{t('contact_details.actions')}</Text>

          <Box pb="16px">

            <HStack
              className={style.action}
              onClick={() => {

              }}
            >
              <Box>
                <Text variant="regular">{t('contact_details.makePayment')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack
              className={style.action}
              onClick={() => {

              }}
            >
              <Box>
                <Text variant="regular">{t('contact_details.paymentHistory')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack
              className={style.action}
              onClick={() => {
                open(AddContact, {
                  isEdit: true
                });
              }}
            >
              <Box>
                <Text variant="regular">{t('contact_details.editContactDetails')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

            <HStack className={style.action} onClick={deleteContact}>
              <Box>
                <Text variant="regular">{t('contact_details.deleteContact')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
            </HStack>

          </Box>
        </Box>
      </MiniBox>
    </Box>
  );
};

export default ContactDetails;
